.title {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: #3f51b5;
  z-index: 1;

  h1 {
    margin: 0;
    text-transform: uppercase;
    font-size: 36px;
    color: #fff;
  }
}